.footer {
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
}

.f-content {
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
}

.f-icons {
    gap: 2rem;
    display: flex;
}

@media screen and (max-width: 820px) {
    .f-content {
        transform: scale(0.5);
    }
}