.services {
    padding: 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
}

.awesome {
    display: flex;
    flex-direction: column;
    position: relative;
}

.awesome>:nth-child(1) {
    font-size: 2.5rem;
    color: var(--black);
    font-weight: bold;
}

.awesome>:nth-child(2) {
    font-size: 2.5rem;
    color: var(--orange);
    font-weight: bold;
}

.awesome>:nth-child(3) {
    font-size: 14px;
    color: var(--grey);
    margin-top: 1rem;
}

.s-button {
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}

.cards {
    position: relative;
}

.cards>* {
    position: absolute;
}

/* blur */
.s-blur1 {
    top: 13rem;
    left: -18rem;
}

.s-blur2 {
    top: 14rem;
    left: 8rem;
}





@media screen and (max-width: 820px) {
    .services {
        margin-top: 0;
        flex-direction: column;
        gap: 5rem;
        height: 66rem;
        padding: 0;
    }

    .cards {
        display: flex;
        flex-direction: column;
        gap: 17rem;
    }

    .cards>* {
        position: static;
    }
}

