.n-wrapper {
    height: 10vh;
    display: flex;
    justify-content: space-around;
}

.n-left {
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;       
}

.n-name {
    font-size: 1.3rem;
    font-weight: bold;
}

.n-right {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list {
    flex: 10;
}

.n-list>ul {
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    list-style-type: none;
    cursor: pointer;
}

.n-list>ul>li:hover {
    cursor: pointer;
    color: var(--orange);
} 

.n-button {
    flex: 2;
}

.mob-wrapper {
    display: none;
}

@media screen and (max-width: 820px) {
  .n-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    
  }

  .n-button {
    display: none;
  }
}

@media screen and (max-width: 480px){
  .n-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
  }

  .n-list {
    display: none;
  }

  .n-button {
    display: none;
  }

  .mob-wrapper {
    display: block;
    padding-left: 3rem;
    }

  .mob-nav {
    display: none;
    position: absolute;
    padding-top: 10px;
    }
}

