.floatingsdiv {
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxshadow);
    border-radius: 17px;
    align-items: center;
    padding: 0 28px 0 0;
    height: 4.5rem;
}

.floatingsdiv>img {
    transform: scale(0.4);
}

.floatingsdiv>span {
    font-family: sans-serif;
    font-size: 16px;
    color: black;
}